<template>
  <header :class="{login:isLogin,'no-login':!isLogin}">
    <!--改变classname通过isLogin的布尔值 -->
    <template v-if="!isLogin">
      <h1><router-link to="/"> Let's share</router-link></h1>
      <p>博客</p>
      <div class="btns">
        <el-button ><router-link to="/login">立即登陆</router-link></el-button>
        <el-button><router-link to="/Register">注册账号</router-link></el-button>
      </div>
    </template>
    <template v-if="isLogin">
      <h1><router-link to="/"> Let's share</router-link></h1>
      <ul class="nav">
        <router-link to="/">首页</router-link>
        <router-link to="/create">创建博客</router-link>
        <router-link to="/my">我的博客</router-link>
      </ul>
      <router-link to="/create"><i class="edit el-icon-plus"></i></router-link>
      <div class="use">
        <img :src="user.avatar" class="avatar" :alt="user.username" :title="user.username">
        <ul>
          <li><router-link to="/my">我的</router-link></li>
          <li><a href="#" @click="onLogout">注销</a></li>
        </ul>
      </div>
    </template>
  </header>
</template>

<script>
import auth from "@/api/author";


import { mapGetters, mapActions } from "vuex";
export default {
  data() {
    return {};
  },
  computed: {
    ...mapGetters([
      "isLogin",
      "user"
      ])
  },
  created() {
    this.checkLogin();
  },
  methods: {
    ...mapActions([
      "checkLogin",
      "logout"
      ]),
    onLogout(){
      this.logout()
    }
  }
};



</script>




<style lang="less">
@import "../assets/base.less";

header{
  .nav{
    flex:2;
    a{
      padding: 0 20px;
      color:#333;
      text-decoration: none;
    }
  }
}

header.no-login {
  padding: 0 12% 30px 12%;
  background: @bgColor;
  display: grid;
  justify-items: center;
  a {
    text-decoration: none;
  }
  h1 {
    a{
      color: #fff;
      font-size: 40px;
      margin: 60px 0 0 0;
      text-transform: uppercase;
      }
  }

  p {
    margin: 15px 0 0 0;
    color: #fff;
    font-size: 25px;
  }
  .btns {
    margin-top: 20px;
  }

  button {
    margin: 20px 5px 0;
  }
}
header.login {
  background: @bgColor;
  display: flex;
  align-items: center;

  h1 {
    font-size: 40px;
    margin: 0;
    text-transform: uppercase;
    flex: 1; //父容器的剩余空间分成1份全给h1,应为其他子元素没有设置flex-grow
    a{
      text-decoration: none;
      color: #fff;
    }
  }
  .edit {
    color: #fff;
    font-size: 30px;
  }

  .avatar {
    width: 40px;
    height: 40px;
    border: 1px solid #fff;
    border-radius: 50%;
    margin-left: 15px;
  }
  .use {
    position: relative;
    ul {
      display: none;
      position: absolute;
      list-style: none;
      right: -15px;
      top:35px;
      padding: 0;
      li {
        padding: 10px;
        border: 1px solid;
        background-color: #fff;
        &:hover {
            background-color: #eaeaea;
          }
        a {
          text-decoration: none;
          white-space: nowrap;
          color: #333;
          font-size: 14px;
          display: block;
          
        }
      }
    }
    &:hover ul{
        display: block;
    }
  }
}
</style>



