import blog from "@/api/blog";
import { mapGetters } from "vuex";

export default {
  data() {
    return {
      blogs: {},
      page: 1,
      total: 1,
    }
  },
  computed: {
    ...mapGetters(['user'])//使用vuex的计算属性
  },
  created() {
    //this.userId = this.$store.state.auth.user.id
    this.page = parseInt(this.$route.query.page) || 1
    blog.getBlogsByUserId(this.user.id, { page: this.page })
      .then(res => {
        this.blogs = res.data
        this.page = res.page
        this.total = res.total
      })
  },
  methods: {
    async deleteBlog(blogId) {
      await this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        type: 'warning'
      })
      await blog.deleteBlog({blogId:blogId})
      this.$message.success('删除成功!')
      this.blogs = this.blogs.filter(blog=>{blog.id!== blogId})//vue的根本修改data,页面响应
    },
    onPageChange(newPage) {
      blog.getIndexBlogs({ page: newPage }).then(res => {
        this.blogs = res.data
        this.page = res.page
        this.total = res.total
        this.$router.push({ path: '/my', query: { page: newPage } })
      })
    },
    splitDate(datsStr) {
      let dateObj = typeof datsStr === "object" ? datsStr : new Date(datsStr)
      return {
        date: dateObj.getDate(),
        month: dateObj.getMonth() + 1 + '月',
        year: dateObj.getFullYear()
      }
    }
  }
}


