import { mapActions } from "vuex";

function debounce(func, delay) {
  let timer
  return (...args) => {
      if (timer) {
          clearTimeout(timer)
      }
      timer = setTimeout(() => {
          func.apply(this, args)
      }, delay)
  }
}



export default {
  data() {
    return {
      username: '',
      password: '',
      nameError:'',
      passwordError:''
    }
  },
  created(){
    this.$watch('username',debounce(()=>{
      let result = /\w{1,15}/.test(this.username)
      if(!this.username){
        this.nameError = '请输入用户名'
      }else if(!result){
        this.nameError = '输入1-15位数字或字母'
      }else{
        this.nameError = ''
      }//this.name?(result?this.nameError = '':this.nameError = '输入1-15位数字或字母'):this.nameError = '请输入用户名'
    },3000)),
    this.$watch('password',debounce(()=>{
      let result = /\w{6,15}/.test(this.password)
      if(!this.password){
        this.passwordError = '请输入密码'
      }else if(!result){
        this.passwordError = '输入6-15位数字或字母'
      }else{
        this.passwordError = ''
      }//this.name?(result?this.nameError = '':this.nameError = '输入1-15位数字或字母'):this.nameError = '请输入用户名'
    },3000))
  },
  methods: {
    ...mapActions([
      'register'
    ]),
    onRegister() {//登陆按钮点击的方法
      this.register({ username: this.username, password: this.password })
        .then(() => {
          this.$router.push({ path: '/' })
        })
    },
  }
}


