//比如封装AJAX
import axios from 'axios'
import {Message} from 'element-ui'//ui组件的消息提醒模块

axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded' //设置全局axios默认值请求头
axios.defaults.baseURL = "http://blog-server.hunger-valley.com" //跨域请求
axios.defaults.withCredentials = true //跨域请求携带cookie

export default function request(url,type='GET',data={}){//输出函数
    return new Promise((resolve,reject)=>{//创建promise对象
        let option = {
            url,
            method:type
        }
        if(type.toLowerCase()==='get'){
            option.params = data
        }else{
            option.data =data
        }
        axios(option).then(res =>{
            if(res.data.status=== 'ok'){//前后端约定后端返回值内容
                resolve(res.data)
            }else{
                Message.error(res.data.msg)
                reject(res.data)
            }
        }).catch(err=>{
            Message.error('网络异常')//element组件
            reject({msg:'网络异常'})
        })
    })
}


