import { mapActions } from 'vuex';

export default {
  data() {
    return {
      username: '',
      password: ''
    }
  },
  methods: {
    ...mapActions([
      'login'
    ]),
    onLogin() {
      this.login({ username: this.username, password: this.password })
        .then(() => {
          this.$router.push({ path: this.$route.query.redirect || '/' })//要求登陆有两种可能，一种是访问有登陆条件的页面时被路由跳转到登陆页面，登陆后要返回有登陆条件的页面
        })
    }
  }
}


