import blog from "@/api/blog";

export default {
  data() {
    return {
      blogs: [],
      user: {},
      page: 1,
      total: 0,
    }
  },
  created() {
    this.userId = this.$route.params.userId
    this.page = this.$route.query.page || 1
    blog.getBlogsByUserId(this.userId, { page: this.page }).then(res => {
      this.blogs = res.data
      this.page = res.page
      this.total = res.total
      if (res.data.length > 0) {
        this.user = res.data[0].user
      }
    })
  },
  methods: {
    onPageChange(newPage) {
      blog.getBlogsByUserId(this.userId, { page: newPage }).then(res => {
        this.blogs = res.data
        this.page = res.page
        this.total = res.total
        this.$router.push({ path: `/user/${this.userId}`, query: { page: newPage } })//用户页面点击下一页，router添加
      })
    },
    splitDate(datsStr) {
      let dateObj = typeof datsStr === "object" ? datsStr : new Date(datsStr)
      return {
        date: dateObj.getDate(),
        month: dateObj.getMonth() + 1 + '月',
        year: dateObj.getFullYear()
      }
    }
  }
}


