import request from '@/pages/helpers/request.js';
import author from '@/api/author.js'
import blog from '@/api/blog.js'



export default {
  data() {
    return {
      blogs:[],
      total:0,
      page:1
    }
  },
  created:function(){
    this.page = parseInt(this.$route.query.page)|| 1//如果存在query,既不是首页，页面渲染的是page所在的,根据url渲染
    blog.getIndexBlogs({page:this.page}).then(res=>{
      this.blogs = res.data
      this.page = res.page
      this.total = res.total
    })
  },
  methods:{
    onPageChange(newPage){
      blog.getIndexBlogs({page:newPage}).then(res=>{
        this.blogs = res.data
        this.page = res.page
        this.total = res.total
        this.$router.push({path:'/',query:{page:newPage}})
      })
    }
  }
}




