import Vue from 'vue'
import Router from 'vue-router'

import login from '@/pages/login/template.vue'
import index from '@/pages/index/template.vue'
import Register from '@/pages/Register/template.vue'
import user from '@/pages/user/template.vue'
import Create from '@/pages/Create/template.vue'
import Detail from '@/pages/Detail/template.vue'
import Edit from '@/pages/Edit/template.vue'
import My from '@/pages/My/template.vue'
import store from '../store';

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      component: index
    },
    {
      path: '/login',
      component: login
    },
    {
      path: '/register',
      component: Register
    },
    {
      path: '/user/:userId',
      component: user
    },
    {
      path: '/detail/:blogId',
      component: Detail
    },
    {
      path: '/create',
      component: Create,
      meta: { requiresAuth: true }
    },
    {
      path: '/edit/:blogId',
      component: Edit,
      meta: { requiresAuth: true }
    },
    {
      path: '/my',
      component: My,
      meta: { requiresAuth: true }
    },
  ]
})

router.beforeEach((to, from, next) => {//每一次路由切换,遍历所有路径
  if (to.matched.some(record => record.meta.requiresAuth)) {//如果路径中有meta信息，说明要判断路由的前提条件
    store.dispatch('checkLogin').then((isLogin) => {//注册的判断得是异步请求，isLogin是布尔值，是async返回的promise
      if (!isLogin) {//自己定义的判断登录的方法，查看库中的状态，
        next({
          path: '/login',//没有登陆页面跳转到登陆页面
          query: { redirect: to.fullPath }
        })
      } else {//如果登陆直接跳转
        next()
      }
    })
  } else {
    next() // 确保一定要调用 next()
  }
})

export default router 


