import author from '@/api/author'

const state = {
    user: null,
    isLogin: false
}
const getters = {//使得页面中直接出现getters,不用从this.$store.state.user中拿
    user: state => state.user,
    isLogin: state => state.isLogin
}
const mutations = {
    setUser(state, payload) {
        state.user = payload.user
    },
    setLogin(state, payload) {
        state.isLogin = payload.isLogin
    }
}
const actions = {
    login({ commit }, { username, password }) {//登陆时要发送ajax，跟据返回的信息更改
        return author.login({ username, password })
            .then(res => {
                commit('setUser', { user: res.data })
                commit('setLogin', { isLogin: true })
            })
    },
    async register({ commit }, { username, password }) {
        let res = await author.register({ username, password })//带上数据对象，发送axios请求，返回值赋值给res
        commit('setUser', { user: res.data })//修改stoe.state中user的状态，由于getters被header组件使用，header会根据state的user显示用户信息
        commit('setLogin', { isLogin: true })
        return res.data
    },
    async logout({commit}) {
        await author.logout()
        commit('setUser', { user: null })
        commit('setLogin', { isLogin: false })
    },
    async checkLogin({ commit, state }) {//
        if (state.isLogin) return true //登陆时直接return true
        let res = await author.getInfo() //state.isLogin是false,获取后台信息
        commit('setLogin', { isLogin: res.isLogin })//页面显不显示登陆状态，取决于后台
        if (!res.isLogin) return false //后台显示不登陆返回flase
        commit('setUser', { user: res.data })//后台显示登陆，根据返回的数据显示用户信息
        return true // async返回promise
    }
}


export default {
    state,
    getters,
    mutations,
    actions
}
    


